}.cover{
    height: 30%;
    background-color: black;
    position: relative;
    display: block;
    

    img {
        object-fit: cover;

        width: 100%;
        height: 50vh;
        min-height: 200px;
    }


}

.text_under_cover{
    display: flex;
    flex-direction: column;
    background-color: #009245;
    padding: 70px 150px;

    color: white;

    @media (max-width: 1200px) {
        padding: 100px;
    }
}
.home_flex_section{
    height: auto;

}

.home_flex_picture{
    display: flex;
    flex-direction: row;
    gap: 30px;
    font-size: 1.3rem;

    @media (max-width: 1200px) {
        flex-direction: column;
    }
}


.home-buttons {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 20px;
    align-items: center;
    text-align: center;
    width: 100%;
    margin: auto;
    position: absolute;
    bottom: 10%;
   

    @media (max-width: 768px) {
        gap: 10px;
    }
}
.home_flex_picture{

}

.home-cover-button {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    text-align: center;
    max-width: 500px;
    color: black;
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
    transition-duration: $transition-hover-md;
    border: 2px solid $white;
    padding: 10px 20px;
    backdrop-filter: blur(5px);

    &:hover {
        background: #98d69d;
    }

    svg {
        font-size: 3rem;
    }
}
