$border-md: 20px;

.grid-item  {
  background-color: #009245;
  border-radius: $border-md; /* You can adjust the value to change the degree of rounding */
  aspect-ratio: 9/5;

  /* Shared styles for video sections */
  .grid-item__content {
    position: relative;
    background-color: aqua;
    display: block;
    justify-content: center;
    height: 100%;
    border-radius: $border-md; /* You can adjust the value to change the degree of rounding */


    img {
      border-radius: 0 0 $border-md $border-md; /* You can adjust the value to change the degree of rounding */
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
}


.upper-section {


  }

.text-upper-section{
  font-size: 1.5rem;
  font-weight: bold;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  height: auto;
  color: white;
  
}  
.button-grid-item{
  margin-left: 10%;
  height: 30px;
  border-radius: 50%;
  aspect-ratio: 1/1;
  border-style: solid;
  border-width:2.5px;
  border-color:black;
  background-color: white;
  color: black;
  font-family: "Comfortaa";
  font-size: 1rem;
  font-weight: bolder;
  transition-duration: 0.4s;
  cursor: pointer;
  &:hover{
    height: 35px;
    background-color: #46ac42;
  }
}



.filter-tags{
  height: auto;
  padding: 1rem;
  margin-bottom: 1rem;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  font-size: 0.8rem;
  color: white;
  // background-color: lightgray;


  ul {
    display: flex;
    flex-direction: row;
    overflow-x: scroll;
    gap: 10px;
    scrollbar-width: none;



    &::-webkit-scrollbar {
      display: none;
      -webkit-appearance: none;
      width: 0px;
      background: transparent;
      
    }

    li {
      border-radius: 10px;
      background-color: #009245;
      border: 2.5px solid black;
      transition-duration: 0.3s;
      padding: 0.5rem;
      
      &:hover {
        background-color: #8bc53e;
      }

    }

  }
}



.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
  height: auto; 
  gap: 2rem;

  @media only screen and (min-width: 1200px) {
    grid-template-columns: repeat(3, minmax(20rem, 1fr));
    gap: 4rem;
    }

}


  